.formControl {
   .childrenWrapperStyle {
      display: grid;
   }
   display: flex;
   flex-direction: column;
   &.error span {
         display: flex;
         align-items: center;
         justify-content: flex-start;
         width: 100%;
         padding: 10px;
         color: red;
         font-size: 12px;
   }
}
